'use strict';
// 打开客服

/**
 * todo CUSTOMER
 */
import {WinJSDec, strError} from './APP/config';

var name = 'CUSTOMER_OPEN';
var CUSTOMER_OPEN = url => {
  if (typeof url !== 'string') {
    throw new TypeError(`[${name}] ${strError}`);
  }
  WinJSDec(_ => {
    window.WinJSBridge.call('customerService', 'showService', {url});
  });
};
export default CUSTOMER_OPEN;
